@import url("https://fonts.googleapis.com/css?family=Oxygen:300");
body {
  color: #0D1921;
  font-family: 'Oxygen';
  font-weight: light;
  letter-spacing: 0.25px;
  overflow-y: scroll;
  transition: 1s opacity ease-in-out;
  margin: 0;
  scroll-behavior: smooth; }

a {
  text-decoration: none; }

.bodyUpdating {
  opacity: 0;
  transition: 0.25s opacity ease-in-out; }

.emailLink,
.scrollToTopLink {
  color: #0D1921;
  cursor: pointer;
  letter-spacing: 0.5px;
  padding: 2px 5px;
  position: fixed;
  text-transform: uppercase;
  transition: 0.5s background-color ease-in-out, 0.25s color ease-in-out;
  z-index: 1; }

.emailLink {
  left: -5px; }
  @media (max-width: 768px) {
    .emailLink {
      padding: 0;
      font-size: 24px;
      position: relative;
      height: 0;
      width: 100vw;
      top: 70vh;
      display: flex;
      align-items: center;
      justify-content: center; }
      .emailLink::before {
        content: '@'; } }
  @media (min-width: 769px) {
    .emailLink {
      top: 50vh;
      font-size: 10px;
      transform: rotate(270deg); }
      .emailLink::before {
        content: 'EMAIL'; } }
  .emailLink:hover {
    background-color: #0D1921;
    color: #FFF;
    text-decoration: none;
    transition: 0.5s background-color ease-in-out, 0.25s color ease-in-out; }

.scrollToTopLink {
  left: auto;
  right: -25px;
  opacity: 0;
  transition: 0.25s opacity ease-in-out; }
  @media (min-width: 769px) {
    .scrollToTopLink {
      top: calc(50vh - 10px);
      font-size: 10px;
      transform: rotate(90deg); }
      .scrollToTopLink::before {
        content: 'BACK TO TOP'; } }

.scrollToTopLink_Visible {
  opacity: 1;
  transition: 0.25s opacity ease-in-out; }

.section {
  align-items: center;
  display: flex;
  height: 110vh;
  justify-content: center;
  min-height: 300px; }
  .section.intro {
    height: 100vh; }
  .section + .section {
    margin-top: 50px; }
  @media (max-width: 1023px) {
    .section {
      min-height: auto;
      height: inherit; } }

.intro.section {
  flex-direction: column; }

.gallery[data-captions="bottom"] .section {
  flex-direction: column-reverse; }

.gallery .image,
.gallery .video {
  border: 1px solid white;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.05), 0 8px 10px rgba(0, 0, 0, 0.03);
  max-height: calc(100vh - 100px);
  max-width: calc(95vw - 20px);
  z-index: 2; }
  @media (max-width: 768px) {
    .gallery .image,
    .gallery .video {
      max-width: calc(100vw - 30px); } }

.video {
  height: auto;
  width: auto; }

.gallery .sidebar {
  position: absolute;
  right: 5vw;
  margin-top: 0vh;
  opacity: 0.9;
  transition: opacity 0.5s ease-in-out;
  z-index: 5; }
  @media (max-width: 768px) {
    .gallery .sidebar {
      right: 0vw;
      margin-top: 25px; } }
  .gallery .sidebar:hover {
    opacity: 0;
    transition: opacity 1s ease-in-out; }

[data-captions="bottom"] .sidebar {
  position: inherit;
  margin-top: 15px;
  padding-top: 0; }

.headline {
  font-size: 24px;
  text-align: center;
  padding: 10px; }

.gallery .headline {
  background: #FFF;
  padding: 0 5px;
  text-align: left;
  z-index: 100; }
  @media (max-width: 1023px) {
    .gallery .headline {
      font-size: 18px; } }

.subtitle {
  padding: 5px 5px;
  margin-top: 1px;
  font-size: 12px; }

.gallery .subtitle {
  opacity: 1;
  background: #FFF;
  min-width: 150px;
  line-height: 1; }

[data-captions="bottom"] .subtitle {
  min-width: auto;
  text-align: center;
  padding: 0 50px;
  line-height: 1.4; }

.sidebar .moreInfo {
  margin-top: 25px;
  font-size: 12px;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0.75);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.05); }

.scrollIcon {
  text-align: center;
  height: 48px;
  position: relative;
  top: -15vh;
  cursor: pointer;
  margin-bottom: -48px; }

.scrollArrows {
  margin: 0 auto;
  width: 24px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 5px;
  animation: 0.5s all ease-in-out; }
  .scrollArrows:hover {
    background: #FAFAFA;
    animation: 0.5s all ease-in-out; }
    .scrollArrows:hover path {
      animation: 0.5s all ease-in-out; }

.scrollArrows svg {
  width: 8px;
  height: 8px;
  display: block;
  animation-timing-function: ease-in-out;
  animation: opacity-wave 2s infinite; }

@keyframes opacity-wave {
  0%,
  80%,
  100% {
    opacity: 0; }
  40%,
  50% {
    opacity: 1; } }
  .scrollArrows svg:nth-of-type(2) {
    animation-delay: 0.25s; }
  .scrollArrows svg:nth-of-type(3) {
    animation-delay: 0.50s; }

.gallery__pagination {
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: center;
  margin-bottom: 50px;
  padding: 5px;
  color: #888; }
  .gallery__pagination a,
  .gallery__pagination a:focus,
  .gallery__pagination a:visited {
    color: #888; }
  .gallery__pagination a:hover {
    color: #222;
    text-decoration: none; }

.gallery__pageNumber a,
.gallery__pageArrow a,
.gallery__pageNumber__current {
  display: inline-block;
  padding: 12px; }

.gallery__pageArrow a:hover {
  text-decoration: none; }

.gallery__pageNumber__current {
  color: #333;
  font-size: 24px; }

.embedContainer {
  position: relative;
  padding-bottom: 48.55%;
  height: 0;
  overflow: hidden;
  margin: 100px 32px; }

.embedContainer iframe,
.embedContainer object,
.embedContainer embed {
  position: absolute;
  top: 0;
  left: 5%;
  width: 90%;
  height: 100%; }
