.galleryImage {
  align-items: center;
  display: flex;
  height: 110vh;
  justify-content: center;
  min-height: 300px; }
  .galleryImage + .galleryImage {
    margin-top: 50px; }
  @media (max-width: 1023px) {
    .galleryImage {
      min-height: auto;
      height: inherit; } }

.galleryImg,
.galleryVideo {
  border: 1px solid white;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.05), 0 8px 10px rgba(0, 0, 0, 0.03);
  max-height: calc(100vh - 100px);
  max-width: calc(95vw - 20px);
  z-index: 2; }
  @media (max-width: 768px) {
    .galleryImg,
    .galleryVideo {
      max-width: calc(100vw - 30px); } }

.galleryVideo {
  height: auto;
  width: auto; }

.galleryImage .sidebar,
.galleryVideo .sidebar {
  position: absolute;
  right: 5vw;
  margin-top: 0vh;
  opacity: 0.9;
  transition: opacity 0.5s ease-out;
  z-index: 5; }
  @media (max-width: 768px) {
    .galleryImage .sidebar,
    .galleryVideo .sidebar {
      right: 0vw;
      margin-top: 25px; } }
  .galleryImage .sidebar:hover,
  .galleryVideo .sidebar:hover {
    opacity: 0;
    transition: opacity 0.75s ease-in-out; }

.sidebar.bottom {
  position: inherit;
  margin-top: 15px;
  padding-top: 0; }

.headline {
  font-size: 24px;
  text-align: center;
  padding: 10px; }

.gallery .headline {
  background: #FFF;
  padding: 0 5px;
  text-align: left;
  z-index: 100; }
  @media (max-width: 1023px) {
    .gallery .headline {
      font-size: 18px; } }

.subtitle {
  padding: 5px 5px;
  margin-top: 1px;
  font-size: 12px; }

.gallery .subtitle {
  opacity: 1;
  background: #FFF;
  min-width: 150px;
  line-height: 1; }

.bottom .subtitle {
  min-width: auto;
  text-align: center;
  padding: 0 50px;
  line-height: 1.4; }

.sidebar {
  position: absolute;
  right: 5vw;
  margin-top: 0vh;
  z-index: 5; }
  @media (max-width: 768px) {
    .sidebar {
      right: 0vw;
      margin-top: 0vh;
      padding-top: 40vh; } }

.sidebar .moreInfo {
  margin-top: 25px;
  font-size: 12px;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0.75);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.05); }

