.embedContainer {
  position: relative;
  padding-bottom: 48.55%;
  height: 0;
  overflow: hidden;
  margin: 100px 32px;
  width: 100%; }

.embedContainer iframe,
.embedContainer object,
.embedContainer embed {
  position: absolute;
  top: 0;
  left: 5%;
  width: 90%;
  height: 100%; }
  @media (max-width: 768px) {
    .embedContainer iframe,
    .embedContainer object,
    .embedContainer embed {
      left: 0;
      width: 100%; } }
